/*-------- About --------*/

.about {

    &__wrapper {
        position: relative;
    }

    &__vision {
        display: flex;
        padding-top: 174px;
        position: relative;
    }

    &__vision-title {
        font-family: $font-r;
        font-size: 30px;
        font-weight: 400;
        color: #161616;
    }

    &__vision-text {
        margin-top: 64px;
        font-size: 19px;
        color: #161616;
        line-height: 1.7;
    }

    &__vision-image-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 449px;
        
        position: relative;
        overflow: hidden;

        border-radius: 5px;
        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);
    }

    &__vision-image-wrapper:before {
        content: '';
        display: block;
        width: 100%;
        padding-top: 95%;
    }

    &__vision-image {
        height: 110%;
        position: absolute;
        top: -10%;
    }

    &__vision-count {
        width: 181px;

        padding-top: 40px;
        padding-bottom: 20px;
        padding-left: 21px;
        padding-right: 57px;

        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);
        background-color: #ffffff;
        border-radius: 5px;

        position: absolute;
        left: -38px;
        bottom: -43px;

        color: #161616;
        font-size: 20px;
        line-height: 1.3;
    }

    &__vision-count strong {
        display: block;
        margin-bottom: 14px;
        font-family: $font-b;
        font-size: 40px;
        line-height: 1;
    }

    &__vision-column--1 {
        padding-top: 10px;
    }

    &__vision-column--2 {
        padding: 10px;
        padding-left: 100px;
        padding-bottom: 53px;
        flex-shrink: 0;
        position: relative;
    }

    &__title {
        font-family: $font-pf;
        font-size: 64px;
        text-align: center;
    }

    &__title--values {
        margin-top: 159px;   
    }

    &__values {
        display: flex;
        justify-content: space-between;
        padding-top: 80px;
        padding-bottom: 143px;
    }

    &__value {
        width: calc(25% - 8px);
        padding: 31px 24px 49px 24px;
        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);
        background-color: #ffffff;
        border-radius: 10px;
    }

    &__value-icon-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 97px;
        height: 97px;

        border-radius: 50%;
        background-color: #f3f3f3;
    }

    &__value-icon {
        width: 100%;
    }

    &__value-title {
        margin-top: 42px;
        font-family: $font-sb;
        font-size: 27px;
    }

    &__value-text {
        margin-top: 35px;
        font-size: 19px;
        line-height: 1.6;
    }

    &__expertise {
        padding-top: 140px;
        padding-bottom: 164px;
        position: relative;
    }

    &__expertise-list {
        display: flex;
        justify-content: space-between;
        margin-top: 100px;
        font-family: $font-sb;
    }

    &__expertise-item:not(:last-child) {
        padding-right: 30px;
    }

    &__expertise-title {
        display: flex;
        align-items: center;
        font-size: 27px;
    }

    &__expertise-icon {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 97px;
        height: 97px;
        margin-right: 30px;
        border-radius: 50%;
        background-color: #f3f3f3;
    }

    &__expertise-icon img {
        width: 75%;
    }

    &__expertise-text {
        margin-top: 48px;
        padding-left: 17px;
    }

    &__expertise-text-item {
        display: flex;
        align-items: center;
        font-size: 19px;
        color: #161616;
    }

    &__expertise-text-item:before {
        content: '';
        width: 6px;
        height: 6px;
        margin-right: 18px;

        border-radius: 50%;
        background-color: #2a9df4;
    }

    &__expertise-text-item:not(:first-child) {
        margin-top: 25px;
    }

    &__title--nav {
        color: #ffffff;
        font-family: $font-sb;
        font-size: 35px;
        text-align: center;
    }

    &__expertise-nav {
        width: calc(100% + 40px);
        margin-left: -20px;
        padding: 20px 10px;
        padding-bottom: 30px;
        position: -webkit-sticky;
        position: sticky;
        top: 90px;
        z-index: 2;
        background-color: #1d4693;
    }

    &__expertise-navlist {
        display: flex;
        justify-content: center;
        margin-top: 30px;
    }

    &__expertise-navitem:not(:first-child) {
        margin-left: 30px;
    }

    &__expertise-navlink {
        padding-bottom: 7px;
        font-family: $font-sb;
        font-size: 16px;
        color: #8aa0ca;
        border-bottom: 2px solid hsla(0,0%,83.5%,.36470588235294116);
        text-align: center;
    }

    &__expertise-navlink.active {
        color: #fff;
        border-color: #fff;
    }

    &__title--small {
        font-family: $font-sb;
        font-size: 27px;
    }

    &__clients {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 75px;
    }

    &__client {
        display: flex;
        align-items: center;
        justify-content: center;
        
        width: calc(25% - 10px);

        position: relative;
        border: 2px solid transparent;

        transition: border-color 0.3s cubic-bezier(0.77, 0, 0.175, 1);
    }

    &__client:hover {
        border-color:#0095db;
    }

    &__client:nth-child(n+5) {
        margin-top: 15px;
    }

    &__client:before {
        content: '';
        display: block;
        width: 100%;
        padding-top: 100%;
    }

    &__client-background {
        width: 100%;
        height: 100%;
        background-color: #f9f9f9;

        position: absolute;
    }

    &__client-logo--main {
        width: 73%;
        position: absolute;
        z-index: 2;
    }

    &__client-logo--small {
        width: 55%;
    }

    &__client-hidden {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        
        width: 100%;
        height: 100%;
        padding: 40px;

        position: absolute;
        z-index: 2;

        background-color: #ffffff;

        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 0);
        clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 0);

        -webkit-transition: -webkit-clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
        transition: -webkit-clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
        -o-transition: clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
        transition: clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
        transition: clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1), -webkit-clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
    }

    &__client-link {
        margin-top: 32px;
        font-family: $font-sb;
        font-size: 19px;
        color: #8f8f8f;
        letter-spacing: 0.76px;
    }

    &__client:hover &__client-hidden {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }

    &__approach {
        position: relative;
        padding-top: 253px;
        padding-bottom: 139px;
    }

    &__approach-wrapper {
        position: relative;
        z-index: 1;
    }

    &__approach-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 155px;
    }

    &__approach-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: calc(33% - 40px);
        text-align: center;
    }

    &__approach-item:nth-child(n+4) {
        margin-top: 80px;
    }

    &__approach-item-icon {
        width: 90px;
        height: 90px;
    }

    &__approach-item-title {
        margin-top: 25px;
        font-size: 19px;
        color: #1d4693;
    }

    &__approach-item-text {
        margin-top: 35px;
        font-size: 19px;
        line-height: 1.6;
    }
}