/*-------- Page-progress --------*/

.page-progress {
    overflow: hidden;

    &__bar {
        display: flex;
        justify-content: center;
        position: fixed;
        left: 0;
        top: 129px;
        width: 100%;
        height: 3px;
        background-color: #3e3e3e;
        opacity: 0.45;
        z-index: 12;

        transition: opacity 0.1s ease-out;
    }

    &__track {
        width: 0;
        height: 3px;
        background-color: #2998e5;
        position: absolute;
        top: 0;
        left: 0;
        transition: width 0.1s ease-out;
    }

    &__image-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 80px;
        height: 80px;
        position: fixed;
        right: 30px;
        bottom: 30px;
        cursor: pointer;
        z-index: 11;

        transition: right 0.5s cubic-bezier(0.8, 0, 0.175, 1);
    }

    &__image {
        width: 100%;
        height: 100%;
        transition: transform 0.1s ease-out;
    }

    &__count {
        font-family: $font-pf;
        font-size: 30px;
        color: #2998e5;
        position: absolute;
        transition: color 0.2s ease-out;
    }

    &.top &__image-wrapper {
        right: -100%;
    }
}

.header--main + .main .page-progress {
    
    &__bar {
        opacity: 0;
    }
}

.header--white + .main .page-progress {
    
    &__bar {
        opacity: 0.45;
    }
}

@media (max-width: 1099px) {

    .page-progress {

        &__bar {
            top: 90px;
        }
    }
}

@media (max-width: 479px) {

    .page-progress {

        &__bar {
            top: 70px;
        }
    }
}